@font-face {
  font-family: 'Chosunilbo_myungjo';
  src: url('https://fastly.jsdelivr.net/gh/projectnoonnu/noonfonts_one@1.0/Chosunilbo_myungjo.woff') format('woff');
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'GangwonEdu_OTFBoldA';
  src: url('https://fastly.jsdelivr.net/gh/projectnoonnu/noonfonts_2201-2@1.0/GangwonEdu_OTFBoldA.woff') format('woff');
  font-weight: normal;
  font-style: normal;
}

.App {
  text-align: center;
  font-family: 'GangwonEdu_OTFBoldA';
}

.black-nav {
  display: flex;
  background-color: black;
  width: 100%;
  color: white;
  padding-left: 20px;
}

div {
  box-sizing: border-box;
}

.list {
  padding-left: 20px;
  text-align: left;
  border-bottom: 1px solid gray;
  display: block;
}

.list h4 {
  cursor: pointer;
}

.list span {
  cursor: pointer;
  padding: 5px;
  background-color: rgb(245, 250, 96);
}


.modal {
  margin-top: 20px;
  padding: 20px;
  background-color: skyblue;
  text-align: left;
}

.my {
  background-color: rgb(238, 180, 238);
  text-shadow: 2px 1px gray;
  padding: 20px;
}

.list button {
  padding: 5px;
  margin-left: 15px;
  background-color: black;
  color: white;
  border-radius: 5px;
  border: none;
}